import React, { useState } from "react";
import facebook from "../../assets/facebook.png";
import linkden from "../../assets/in.svg";
import printrest from "../../assets/printrest.svg";
import twter from "../../assets/twter.svg";
import youtube from "../../assets/youtube.svg";
import logoSmall from "../../assets/logoSmall.png";



export const Footer = () => {
  const slider = ["Realestate", "New homes", "Popular areas", "Popular searches"];

  const data1 = ["Real estate NSW", "Real estate WA", "Real estate ACT", "Real estate Melbourne", "Real estate Adelaide", "Real estate Darwin"]
  const data2 = ["Real estate VIC", "Real estate SA", "Real estate NT", "Real estate Brisbane", "Real estate Hobart"]
  const data3 = ["Real estate QLD", "Real estate TAS", "Real estate Sydney", "Real estate Perth", "Real estate Canberra"];
  const icon = [facebook, linkden, printrest, twter, youtube]
  const navi = [
    {
      id: 1,
      name: "Advertise with us",
      link: "#"
    },
    {
      id: 2,
      name: "Contact us",
      link: "#"
    },
    {
      id: 3,
      name: "Agent admin",
      link: "https://agent.argimb.com.ar"
    },
    {
      id: 4,
      name: "Media sales",
      link: "#"
    },
    {
      id: 5,
      name: "Legal",
      link: "#"
    },
    {
      id: 6,
      name: "Privacy",
      link: "#"
    },
    {
      id: 7,
      name: "Site map",
      link: "#"
    },
    {
      id: 8,
      name: "Careers",
      link: "#"
    }
  ]

  const [isActive, setIsActive] = useState(slider[0]);

  const ActiveTab =
    "bg-[#FFEAEF] border-b-[#E5002A] rounded-t-lg text-[#404040] font-semibold";

  const NormalTab =
    "w-full md:w-[26%]  grid place-items-center whitespace-nowrap  text-[#737373] font-medium text-sm md:text-sm  lg:text-base border border-b-2 border-transparent hover:border-b-[#E5002A] py-3 px-5 ease-in-out duration-700 cursor-pointer";

  return (
    <>
      <footer className="bg-white w-auto pt-4">
        <div className="xl:px-48 px-5">
          <div id="navigation"
            className="w-full grid place-items-start overflow-x-scroll rounded-xl"
          >
            <div className="w-full flex justify-start items-start border border-b-2 border-transparent border-b-[#E5E5E5]">
              {slider?.length > 0 &&
                slider?.map((e, index) => (
                  <div
                    key={index}
                    className={`${NormalTab} ${isActive === e ? ActiveTab : ""}`}
                    onClick={() => {
                      setIsActive(e);
                    }}
                  >
                    {e}
                  </div>
                ))}
            </div>
          </div>
          <div className="py-5 xl:text-lg lg:text-base md:text-sm font-semibold text-[#404040]">Real Estate in Australia</div>

          <div className="grid md:grid-cols-4 grid-cols-1 md:text-sm text-xs text-[#737373] ">
            <div>{data1.map((e,index) => (
              <div key={index} className="py-1 cursor-pointer">{e}</div>
            ))}</div>

            <div>{data2.map((e,inde) => (
              <div key={inde} className="py-1 cursor-pointer" >{e}</div>
            ))}</div>

            <div>{data3.map((e,ind) => (
              <div key={ind} className="py-1 cursor-pointer" >{e}</div>
            ))}</div>

            <div>
            <img
              src={logoSmall}
              alt="logo"
              className=" h-[30px] mt-1 sm:mt-0 sm:h-10 rounded-xl cursor-pointer"
            />
              <div className="my-3 lg:text-base md:text-sm font-semibold text-[#404040] mt-5">realestate.com.au</div>
              <p>Lorem ipsum dolor amet, consectetur adipiscing elit. Eget nisl nunc quam ac sed turpis volutpat. Cursus sed massa non nisi, placerat.</p>
              <div className="flex gap-3 mt-5 mb-14">
                {icon?.map((img,index) => (
                  <img key={index} className="cursor-pointer" src={img} alt="" />
                ))}
              </div>
            </div>
          </div>
        </div>
        <div className="  border-t-2 border-gray-200 text-[#404040] ">
          <div className="flex flex-wrap md:justify-between py-4 md:px-0 px-5" >
            <div className=" mx-auto xl:text-md md:text-sm text-xs ">Copyright  2020 realestate.com.au</div>
            <div className="grid place-content-end md:grid-cols-8 grid-cols-2 xl:pr-12 pr-0  gap-2 md:pt-0 pt-3  md:text-center">
              {navi?.map((data,index) => (
                <a key={index} className="md:text-sm text-xs md:pr-0 pr-8" href={data.link}>{data.name}</a>
              ))}
            </div>
          </div>
        </div>
      </footer>
    </>
  );
};
export default Footer;
